.dragDrop{
  overflow: auto;
}
.dragDropUpdate{
    background-color: orange;
    border-radius: 10px 10px 10px 10px ;
    height: 150%;
    margin-left: 1%;
    height: 36px;
    width: 190px;
    margin-top: 30px;
    @media (max-width: 768px) {
      display: none;
   }
    &:hover{
      box-shadow: 1px 1px 3px 7px rgba(243, 204, 136, 0.511);
      transition: 0.5s;
      background-color:  rgb(255, 185, 64);
    }
}
