///////////////////////// Modal
@keyframes OpenModal {
    from {opacity: 0}
    to {opacity: 1}
  }

.charactersupdatemodalwrapper2{
animation-name: OpenModal;
animation-duration: 1s;
position: fixed;
width: 40%;
left: 35%;
max-height: 1000px;
background-color: rgba(28, 28, 28, 0.808);
bottom: 30%;
border: 1px solid gold;
z-index: 99;
box-shadow: 5px 5px 5px 300px rgba(0, 0, 0, 0.349);
@media (max-width: 1000px) {
    width: 60%;
}
@media (max-width: 600px) {
    width: 100%;
    left: 0%;
    bottom: 25%;
    border-radius:0%;
}
p{
    margin-top: 10px;
    font-size: 30px;
    color: gold;
    text-align: center;
}

.charactersupdatemodal{
    display: flex;
    flex-direction: column; 
    max-height: 500px;
    margin-top: 5%;
    margin-left: 4%;
    overflow: hidden;
    input {
        width: 95%;
        height: 30px;
        background-color:  rgb(240, 230, 217);
        &:hover{
        background-color:  rgb(255, 255, 255);
        transition: 1s;
        }
    }
    .modalButtons{
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        .submitModal{
            width: 200px;
            background-color: rgb(165, 230, 241);
            &:hover{
            background-color: rgb(89, 219, 242);
            }
        }
        .closeModal{
            width: 200px;
            margin-right: 5%;
            background-color: rgb(176, 47, 0);
            margin-bottom: 1%;
            &:hover{
            background-color: rgb(239, 69, 7);
            }
            @media (max-width: 600px) {
                margin-right: 0%;
            }
        }
    }
      .errorsInfoModal{
        color: gold;
        text-align: left;
       }
       textarea{
        width: 95%;
        height: 150px;
        background-color:  rgb(240, 230, 217);
        &:hover{
        background-color:  rgb(255, 255, 255);    
        transition: 2s;
        }
       }
       .notOK{
        color: red;
      }
      .notOkNeedVar {
        color: yellow;
      }
      .Ok{
        color: rgb(121, 255, 4);
      }

}

}
@media only screen and (max-width: 600px) {
    .charactersupdatemodalwrapper2{
       left: 5%;
        width: 90%;
    }
    .sendCharacters-wrapper{
    .sendCharacters-wrapper-form{
        width: 100%;
        margin-top: 10%;
        gap: 5px;
    }
    
    .charactersMenuOptions{
        p{
            font-size: 15px;
            margin-left: 5%;
        }
        i{
            font-size: 30px;
            padding: 20px;
        }
    }
}
 }
 .charactersupdatemodalwrapperclose{
 display: none;
   }