.LatestNewsForm{
  .fromMsg{
    color:rgb(88, 180, 255);
    margin-left: 150px;
    font-size: 46px;
  }
  .addItem{
    margin-left: 1%;
    margin-top: 2%;
    background-color: rgb(0, 140, 255);
    color: white;
    width: 250px;
    height: 50px;
    border-radius: 10px 10px 10px 10px ;
    &:hover{
      background-color: gold;
    }
  }
  .hiddenForm{  
    opacity: 0;
  } 
}

.LatestNewsFormFormwrapper{
   opacity: 1;
   transition: 1s;
  .LatestNewsFormForm{
    margin-top: 1%;
    display: flex;
    flex-direction: column;
    max-width: 30%;
    max-height: 1000px;
    background-color: rgba(0, 0, 0, 0.404);
    @media (max-width: 1000px) {
      max-width: 100%;
  }
    h4{
        font-size: 25px;
        color: gold;
        font-family: Fantasy;
        margin-left: 10px;
    }
    input{
        height: 40px;
        background-color: rgba(243, 233, 233, 0.701);
        color: gold;
        border-radius: 10px 10px 10px 10px ;
    }
    .textAreaAd{
       min-height: 50px;
       background-color: rgba(243, 233, 233, 0.701);
       color: gold;
       border-radius: 10px 10px 10px 10px ;
    }
    .notOK{
      color: red;
    }
    .notOkNeedVar {
      color: yellow;
    }
    .Ok{
      color: rgb(121, 255, 4);
    }
    .LatestNewsFormFormsubmit{
      width: 100px;
      background-color: rgb(124, 176, 0);
      color: black;
      &:hover{
        background-color: rgb(163, 233, 0);
      }
    }
    .LatestNewsFormFormsubmitdisabaled{
      width: 100px;
      background-color: rgb(230, 39, 39);
      color: black;
     
    }
  }
 }
 .submitedInfo{
  color: gold;
  font-size: 30px;
 }
 .textArea-second{
  height: 30px;
  background-color: rgba(243, 233, 233, 0.701);
  color: rgb(121, 255, 4);
 }
 .customfileinput::-webkit-file-upload-button {
  visibility: hidden;
  width: 1px;
}
.customfileinput::before {
  content: 'Uploaded Picture :';
  display: inline-block;
  outline: none;
  white-space: nowrap;
  -webkit-user-select: none;
  cursor: pointer; 
  font-weight: 700;
   color:black;
}
.customfileinput:hover::before {
  border-color: black;
}
.customfileinput:active::before {
  background: -webkit-linear-gradient(top, #e3e3e3, #f9f9f9);
}

.customfileinputempty::-webkit-file-upload-button {
  visibility: hidden;
}
.customfileinputempty::before{
  content: 'Press to Upload Your Picture Here';
  display: inline-block;
  outline: none;
  white-space: nowrap;
  -webkit-user-select: none;
  cursor: pointer;
  text-shadow: 1px 1px rgb(2, 0, 0);
  font-weight: 700;
 color:gold;
 width: 1px;
 margin-right: 100%;
}
